


























import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';

@Component
export default class NumberPad extends Vue {
  @Prop(Number) readonly amount!: number;

  output = this.amount.toString();

  inputContent(event: MouseEvent) {
    const button = (event.target as HTMLButtonElement);
    const input = button.textContent!;
    if (this.output.length >= 16) {window.alert('最多输入16位数字'); return};
    if (this.output === '0' && '0123456789'.indexOf(input) >= 0) {
      this.output = input;
    } else if (this.output.indexOf('.') >= 0 && input === '.') {
      return;
    } else {
      this.output += input;
    }
  }

  remove() {
    if (this.output.length > 1) {
      this.output = this.output.slice(0, -1);
    } else {
      this.output = '0';
    }
  }

  clear() {
    this.output = '0';
  }

  ok() {
    if (this.output[this.output.length - 1] === '.') {
      this.output = this.output.slice(0, -1);
    }
    this.$emit('update:amount', parseInt(this.output));
    this.$emit('submit', parseInt(this.output));
    this.output = '0';
  }
}
